import React, { useState, createContext } from 'react';
import { useLocalStore } from "../services/LocalStoreService";

export const FiatContext = createContext();

export const FiatProvider = (props) => {
    const fiatStore = useLocalStore('fiat', 'EUR')[0];
    const [fiat, setFiat] = useState(fiatStore);

    return (
        <FiatContext.Provider value={[fiat, setFiat]}>
            {props.children}
        </FiatContext.Provider>
    )
}