import React, { useState, useEffect, useContext, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ConfigP1 from './ConfigP1';
import ConfigP2 from './ConfigP2';
import ConfigP3 from './ConfigP3';
import ConfigP4 from './ConfigP4';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { socket } from '../context/SocketContext';
import { useSnackbar } from 'notistack';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { ServerContext } from '../context/ServerContext';
import { BalancesContext } from '../context/BalancesContext';
import { Crypt } from 'hybrid-crypto-js';

const useStyles = makeStyles((theme) => ({
    layout: {
        width: '1000px',
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(6),
            marginBottom: theme.spacing(6),
            padding: theme.spacing(3),
        },
    },
    stepper: {
        padding: theme.spacing(3, 0, 5),
    },
    buttons: {
        display: 'flex',
        justifyContent: 'flex-end',
    },
    button: {
        marginTop: theme.spacing(3),
        marginLeft: theme.spacing(1),
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));

const steps = ['Exchange Details', 'Coin Settings', 'Liquidity Settings', 'General Settings'];

export default function ConfigMain() {
    const classes = useStyles();
    const [configuration, setConfiguration] = useContext(ConfigurationContext);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [activeStep, setActiveStep] = useState(0);
    const [coinsLoaded, setCoinsLoaded] = useState(false);
    const [input, setInput] = useContext(ConfigurationInputContext);
    const setStartingBalance = useContext(StartingBalanceContext)[1];
    const setBalances = useContext(BalancesContext)[1];
    const [open, setOpen] = useState(false);
    const { access, publicKey } = useContext(ServerContext)[0];
    const [dialog2, setDialog2] = useState({ title: '', message: '' });
    const [openDialog2, setOpenDialog2] = useState(false);
    const dialogShownRef = useRef(false);

    let crypt = new Crypt({
        md: 'sha256',
        rsaStandard: 'RSA-OAEP',
    });

    const handleClose = () => {
        setOpen(false);
    };

    useEffect(() => {
        setInput({ ...configuration, recreate: false });

        return () => {
        }
    }, []);

    useEffect(() => {
        if (configuration.apiKey === '***' && configuration.apiSecret === '***' && !dialogShownRef.current) {
            dialogShownRef.current = true;

            setDialog2({
                title: 'Your bot has been restored from a backup',
                message: 'Please re-enter your API details to re-activate your bot',
            });

            setOpenDialog2(true);
        }

        return () => {
        }
    }, [configuration]);

    const action = key => (
        <React.Fragment>
            <Button
                style={{
                    color: '#bbbbbb',
                }}
                onClick={() => { closeSnackbar(key) }}>
                Dismiss
            </Button>
        </React.Fragment>
    );

    const handleNext = () => {
        let error = false;
        if (activeStep === 0) {
            if (input.exchange === '' || input.apiKey === '' || input.apiSecret === '') error = true;
            else getPortfolio();
        }
        if (activeStep === 1) {
            let match = input.portfolio.find(c => c.coin === input.basecoin);
            if (!match) error = true;   // If 'next' is clicked before coins are loaded, match will be false
            if (match && Number(match.distribution) === 0) error = true; // target distribution of basecoin may not be set to zero
            if (!coinsLoaded) error = true;
        }
        if (activeStep === 3) {
            if (input.botname.length === 0) {
                error = true;
                enqueueSnackbar('Bot name must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
            }
            if (Number(input.threshold) === 0) {
                error = true;
                enqueueSnackbar('Threshold must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
            }
            if (input.cooldown === '') {
                error = true;
                enqueueSnackbar('Cooldown trigger must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
            }
            if (input.change24h === '') {
                error = true;
                enqueueSnackbar('Maximum 24h percentage drop must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
            }
            if (!error) {
                setOpen(true);
                storeConfiguration();
                // Wait for server confirmation below
            }
        }
        if (activeStep !== 3 && !error) setActiveStep(activeStep + 1);
    };

    const setPortfolioListener = () => {
        socket.on("portfolio", data => {
            // data is false if the API key or secret are invalid, or if the server's IP address is not whitelisted on exchange
            if (data === false) {
                setActiveStep(0);
                enqueueSnackbar(`Invalid API key or API secret`, {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
                enqueueSnackbar(`Also make sure your machine's IP address is whitelisted for this API key on the exchange`, {
                    variant: 'error',
                    autoHideDuration: 8000,
                    action,
                });
                setPortfolioListener();
            }

            if (input.basecoin !== '' && data.length > 0) {
                setCoinsLoaded(true);
            }
        });
    }

    useEffect(() => {
        setPortfolioListener();

        return () => {
        }
    }, []);

    useEffect(() => {
        socket.on("configuration-stored", () => {
            if (input.apiPassword) {
                setConfiguration((prevState => {
                    return {
                        ...prevState,
                        apiKey: '************',
                        apiSecret: '************',
                        apiPassword: '************',
                    }
                }));
            } else {
                setConfiguration((prevState => {
                    return {
                        ...prevState,
                        apiKey: '************',
                        apiSecret: '************',
                    }
                }));
            }
            // Reset starting balances to trigger reload (in case coins were added to portfolio)
            setStartingBalance([]);
            setOpen(false);
            setActiveStep(4);
        });

        return () => {
            socket.off("configuration-stored");
        }
    }, []);

    const handleBack = () => {
        if (activeStep === 2) {
            // Reactivate listener, as it gets deactivated in step 2
            setPortfolioListener();
            getPortfolio();
        }
        setActiveStep(activeStep - 1);
    };

    const handleInput = (e) => {
        let newInput = input;
        newInput[e.field] = e.input;
        setInput(newInput);
    }

    const getPortfolio = () => {
        if (socket && access.allow) socket.emit("get-portfolio", {
            exchange: input.exchange,
            apiKey: input.apiKey,
            apiSecret: input.apiSecret,
            apiPassword: input.apiPassword,
            basecoin: input.basecoin,
        }, access.token);
    }

    const storeConfiguration = () => {
        // Store numbers as numbers
        input.threshold = Number(input.threshold);
        input.cooldown = parseInt(input.cooldown);
        input.change24h = parseInt(input.change24h);

        // Filter out fields that don't have to be stored
        input.portfolio.forEach((coin) => {
            delete coin.currentDistribution;
            delete coin.number;
            delete coin.price;
        });

        // Remove unneeded field
        delete input.excludedChange;
        delete input.offsetChange;
        delete input.configstate;

        // Filter liquidity changes with number = 0
        let newLiquidity = [];
        input.liquidity.forEach(liq => {
            newLiquidity.push({
                date: liq.date,
                changes: liq.changes.filter(c => c.number !== 0),
            });
        });
        input.liquidity = newLiquidity;

        // Remove days with zero changes
        newLiquidity = [];
        input.liquidity.forEach(liq => {
            if (liq.changes.length !== 0) {
                newLiquidity.push(liq);
            }
        });
        input.liquidity = newLiquidity;

        // Remove suggested field
        let liqlast = input.liquidity[input.liquidity.length - 1];
        if (liqlast) liqlast.changes.forEach(liq => {
            delete liq.suggested;
            delete liq.id;
        });

        // Sort liquidity by date
        input.liquidity.sort((a, b) => (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0));

        if (socket && access.allow) {
            let encryptedInput = {
                ...input,
                apiKey: crypt.encrypt(publicKey, input.apiKey),
                apiSecret: crypt.encrypt(publicKey, input.apiSecret),
            }
            if (input.apiPassword) encryptedInput.apiPassword = crypt.encrypt(publicKey, input.apiPassword);

            socket.emit("store-configuration", encryptedInput, access.token);
            setConfiguration(input);
            setBalances({});
        }
    }

    function getStepContent(step) {
        switch (step) {
            case 0:
                return <ConfigP1 callback={handleInput} />;
            case 1:
                return <ConfigP2 callback={handleInput} />;
            case 2:
                return <ConfigP3 callback={handleInput} />;
            case 3:
                return <ConfigP4 callback={handleInput} />;
            default:
                throw new Error('Unknown step');
        }
    }

    return (
        <div>
            <Backdrop className={classes.backdrop} open={open} onClick={handleClose}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <React.Fragment>
                <CssBaseline />
                <Dialog
                    open={openDialog2}
                    fullWidth={true}
                >
                    <DialogTitle>{dialog2.title}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            {dialog2.message}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button variant="contained" onClick={() => setOpenDialog2(false)} color="secondary">
                            Close
                        </Button>
                    </DialogActions>
                </Dialog>
                <main className={classes.layout}>
                    <Paper className={classes.paper}>
                        <Stepper activeStep={activeStep} className={classes.stepper}>
                            {steps.map((label) => (
                                <Step key={label}>
                                    <StepLabel>{label}</StepLabel>
                                </Step>
                            ))}
                        </Stepper>
                        <React.Fragment>
                            {activeStep === steps.length ? (
                                <React.Fragment>
                                    <Typography variant="h5" gutterBottom>
                                        Configuration has been saved
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        You can now leave this page
                                    </Typography>
                                </React.Fragment>
                            ) : (
                                <React.Fragment>
                                    {getStepContent(activeStep)}
                                    <div className={classes.buttons}>
                                        {activeStep !== 0 && (
                                            <Button onClick={handleBack} className={classes.button}>
                                                Back
                                            </Button>
                                        )}
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            onClick={handleNext}
                                            className={classes.button}
                                        >
                                            {activeStep === steps.length - 1 ? 'Save Configuration' : 'Next'}
                                        </Button>
                                    </div>
                                </React.Fragment>
                            )}
                        </React.Fragment>
                    </Paper>
                </main>
            </React.Fragment>
        </div>
    );
}