import React, { useContext } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import { ServerContext } from '../context/ServerContext';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        height: '68px',
        minWidth: '340px',
    },
}));

export default function StatusView() {
    const classes = useStyles();
    const theme = useTheme();
    const { status, updated } = useContext(ServerContext)[0];

    let statuschip;
    if (status === 'connected') {
        statuschip = <Chip label={status} style={{ backgroundColor: theme.palette.success.dark, color: 'white', width: '90px' }} />
    } else {
        statuschip = <Chip label={status} style={{ backgroundColor: theme.palette.error.dark, color: 'white', width: '110px' }} />
    }
    return (
        <Paper className={classes.paper}>
            <Grid container alignItems="center">
                <Grid container item alignItems="center" xs={7}>
                    <Grid item xs={4}><Typography>Status: </Typography></Grid>
                    <Grid item xs={8}>{statuschip}</Grid>
                </Grid>
                <Grid container item alignItems="center" xs={5}>
                    <Grid item xs={12}><Typography>Updated: <b>{updated && updated.split(' ')[1]}</b></Typography></Grid>
                </Grid>
            </Grid>
        </Paper >
    )
}