import React, { useState, createContext } from 'react';

export const StartingBalanceContext = createContext();

export const StartingBalanceProvider = (props) => {
    const [startingBalance, setStartingBalance] = useState([]);

    return (
        <StartingBalanceContext.Provider value={[startingBalance, setStartingBalance]}>
            {props.children}
        </StartingBalanceContext.Provider>
    )
}