import React, { useState, useContext, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { ServerContext } from '../context/ServerContext';
import { ExchangesContext } from '../context/ExchangesContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';
import { useTheme } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

const useStyles = makeStyles((theme) => ({
    textinput: {
        width: '380px',
    },
    select: {
        width: '380px',
    },
}));

export default function ConfigP1(props) {
    const classes = useStyles();
    const mode = useTheme().palette.type;

    const exchangesWithPassword = ['Bitget', 'Coinbasepro', 'Kucoin', 'Okcoin', 'Okx'];

    const input = useContext(ConfigurationInputContext)[0];
    const [{ serverip }] = useContext(ServerContext);
    const [exchange, setExchange] = useState(input.exchange || '');
    const [apiKey, setApiKey] = useState(input.apiKey || '');
    const [apiSecret, setApiSecret] = useState(input.apiSecret || '');
    const [apiPassword, setApiPassword] = useState(input.apiPassword || null);
    const exchanges = useContext(ExchangesContext)[0];
    const [requirePassword, setRequirePassword] = useState(exchangesWithPassword.includes(input.exchange) || false);

    useEffect(() => {
        setExchange(input.exchange);
        setApiKey(input.apiKey);
        setApiSecret(input.apiSecret);
        setApiPassword(input.apiPassword);

        if (exchangesWithPassword.includes(input.exchange)) {
            setRequirePassword(true);
        }

        return () => {
        }
    }, [input]);

    // Reorder the exchanges so these can be properly shown in two columns
    let merged = [];
    const mid = Math.ceil(exchanges.length / 2);
    const firstHalf = exchanges.slice(0, mid);
    const secondHalf = exchanges.slice(mid);

    for (let i = 0; i < mid; i++) {
        if (firstHalf[i]) merged.push(firstHalf[i]);
        if (secondHalf[i]) merged.push(secondHalf[i]);
    }

    const onInput = (e) => {
        props.callback({
            field: e.target.id,
            input: e.target.value
        });

        if (e.target.id === 'apiKey') setApiKey(e.target.value);
        if (e.target.id === 'apiSecret') setApiSecret(e.target.value);
        if (e.target.id === 'apiPassword') setApiPassword(e.target.value);
    }

    const onExchange = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setExchange(e.target.value);

        if (exchangesWithPassword.includes(e.target.value)) {
            setRequirePassword(true);
        } else {
            setRequirePassword(false);
        }
    }

    const [open, setOpen] = useState(false);

    const handleTooltipClose = () => {
        setOpen(false);
    };

    const handleTooltipOpen = () => {
        setOpen(true);
    };

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        Exchange Details
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <ClickAwayListener onClickAway={handleTooltipClose}>
                        <HtmlTooltip
                            mode={mode}
                            title={
                                <React.Fragment>
                                    <Box m={2}>
                                        {"If you haven't done so already, logon to your exchange, go to API Management and create a new API key. "}<b>{"Make sure the API key is enabled for spot trading"}</b>{". Then enter the API key and API secret here."}
                                        <div>&nbsp;</div>
                                        {"Also, "}<b>{"make sure your server's IP address is added to the API key's IP whitelist (if enabled)"}</b>{". You can find the IP whitelist under API Management, where you've created your API key. If you run Balance Bot on your PC, then use the IP address of your internet connection, which you can find e.g. here "}<Link href="https://www.whatsmyip.org/" target="_blank" rel="noopener" color="secondary">www.whatsmyip.org</Link>{". If you run Balance Bot in the cloud, then use the IP address of your cloud server."}
                                        <div>&nbsp;</div>
                                        <div><i>{"Note: "}</i>{"you only need to enter the API details once, as they will be stored on the server for later use. If you want to change the configuration settings at a later point, you can just click the Next button and reuse the API details that you've already configured previously. If you want to change to a different API key, you can entering the new details here."}</div>
                                    </Box>
                                </React.Fragment>
                            }
                            onClose={handleTooltipClose}
                            open={open}
                            disableFocusListener
                            disableHoverListener
                            disableTouchListener
                            interactive
                        >
                            <IconButton onClick={open ? handleTooltipClose : handleTooltipOpen}>
                                <InfoOutlinedIcon color="secondary" />
                            </IconButton>
                        </HtmlTooltip>
                    </ClickAwayListener>
                </Grid>
            </Grid>
            <form style={{ minWidth: '100%' }}>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <Grid item xs={5}>
                            <FormControl>
                                <InputLabel>Exchange</InputLabel>
                                <Select
                                    MenuProps={{ classes: { paper: classes.select } }}
                                    required
                                    labelId="exchange"
                                    name="exchange"
                                    value={exchange}
                                    onChange={onExchange}
                                    className={classes.textinput}
                                    disabled={input.configstate !== 'initial'}
                                >
                                    {merged.map((exchange, i) => {
                                        return <MenuItem style={{ display: "inline-flex", width: "190px" }} key={i} value={exchange}>{exchange}</MenuItem>
                                    })}
                                </Select>
                                <FormHelperText>Exchange cannot be changed after initial bot configuration</FormHelperText>
                            </FormControl>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="apiKey"
                            name="apiKey"
                            label="API Key"
                            type="password"
                            value={apiKey}
                            onChange={onInput}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="apiSecret"
                            name="apiSecret"
                            label="API Secret"
                            type="password"
                            value={apiSecret}
                            onChange={onInput}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12} hidden={!requirePassword}>
                        <TextField
                            hidden={!requirePassword}
                            required
                            id="apiPassword"
                            name="apiPassword"
                            label="API Password / Passphrase"
                            type="password"
                            value={apiPassword || ''}
                            onChange={onInput}
                            fullWidth
                        />
                    </Grid>
                </Grid>
            </form>
            <Grid item xs={12}>
                <Box m={4}></Box>
                <Box m={0}>
                    <Typography variant="overline" color="secondary">
                        &gt; Make sure your API key is enabled for spot trading
                    </Typography>
                </Box>
                <Box m={0}>
                    {serverip === '0.0.0.0' ? (
                        <Typography variant="overline" color="secondary">
                            &gt; Make sure your server's IP address is added to the API key's IP whitelist (if enabled)
                        </Typography>
                    ) : (
                        <Typography variant="overline" color="secondary">
                            &gt; Make sure your server's IP address ({serverip}) is added to the API key's IP whitelist (if enabled)
                        </Typography>
                    )}
                </Box>
            </Grid>
        </React.Fragment >
    );
}