import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import FavoriteTwoToneIcon from '@material-ui/icons/FavoriteTwoTone';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
}));

const Support = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container item spacing={3} direction="column" justify="flex-start" alignItems="flex-start">
        <Grid item xs={12}>
          <Typography variant="h3" noWrap>
            Support Us
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Paper style={{ minWidth: '750px', maxWidth: '1000px' }} className={classes.paper}>
            <p>
              We hope that you're enjoying Balance Bot. The Balance Bot team develops the application in their spare time, and they're not getting paid for the work they do. Their wish is to <b>keep Balance Bot for free forever</b>. And you can help.
            </p>
            <p>
              If you're an enthusiastic Balance Bot user, please considering contributing through the options listed <Link href="https://hodlerhacks.com/balance-bot/support-us/" target="_blank" rel="noopener" color="secondary">here</Link>. Thank you so much! <FavoriteTwoToneIcon fontSize="small" color="secondary" />
            </p>
            <p>
              Donations are anonymous, but feel free to let us know about your generosity and <Link href="https://t.me/hodlerhacks" target="_blank" rel="noopener" color="secondary">send us a message</Link>.
            </p>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
export default Support;
