import React, { useState, createContext } from 'react';

export const ConfigurationInputContext = createContext();

export const ConfigurationInputProvider = (props) => {
    const [configurationInput, setConfigurationInput] = useState({});

    return (
        <ConfigurationInputContext.Provider value={[configurationInput, setConfigurationInput]}>
            {props.children}
        </ConfigurationInputContext.Provider>
    )
}