const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
    },
    table: {
        minWidth: 750,
        "& .MuiTableCell-sizeSmall": {
            paddingTop: '9px',
            paddingBottom: '9px',
        }
    },
    tableRow: {
        height: 50,
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
}));

function formatResult(num, dec) {
    // Complex logic needed to have fixed decimals, avoid scientific notation, and to deal with numbers that already have scientific notation
    let result;
    if (!("" + num).includes("e")) {
        result = +(Math.round(num + "e+" + dec) + "e-" + dec);
    } else {
        let arr = ("" + num).split("e");
        let sig = ""
        if (+arr[1] + dec > 0) {
            sig = "+";
        }
        result = +(Math.round(+arr[0] + "e" + sig + (+arr[1] + dec)) + "e-" + dec);
    }

    // let len = result.toString().split('.')[0].length;
    // return Number(result).toFixed(dec + 1 - len);
    return Number(result).toFixed(dec);
}

export default function BalancesTable(props) {
    const classes = useStyles();
    const [selected, setSelected] = useState([]);
    const theme = useTheme();
    const balances = props.balances;
    const threshold = balances.threshold;
    const configuration = useContext(ConfigurationContext)[0];
    const headless = configuration.headless || false;
    const handleOrder = props.onOrder;
    const handleTableUpdate = props.onUpdate;

    const [headCells, setHeadCells] = useState([
        { id: 'coin', align: 'left', width: '60px', disablePadding: true, label: 'Coin', active: false, order: 'asc' },
        { id: 'number', align: 'right', width: '100px', disablePadding: false, label: 'Amount', active: false, order: 'asc' },
        { id: 'last', align: 'right', width: '110px', disablePadding: false, label: 'Price', active: false, order: 'asc' },
        { id: 'value', align: 'right', width: '110px', disablePadding: false, label: 'Value', active: false, order: 'asc' },
        { id: 'distribution', align: 'right', width: '80px', disablePadding: false, label: 'Distr', active: false, order: 'asc' },
        { id: 'target', align: 'right', width: '80px', disablePadding: false, label: 'Target', active: false, order: 'asc' },
        { id: 'delta', align: 'right', width: '80px', disablePadding: false, label: 'Delta', active: false, order: 'asc' },
        { id: 'trade', align: 'center', width: '100px', disablePadding: false, label: 'Trade', active: false, order: 'asc' },
        { id: 'side', align: 'center', width: '100px', disablePadding: false, label: 'Order', active: false, order: 'asc' },
    ]);

    const getBotID = () => {
        const pathname = window.location.pathname;
        const result = pathname.match(/bot[0-9]{3}/);
        return result[0];
    }

    const [rows, setRows] = useState([]);

    const botID = getBotID();
    const defaultsort = {
        botID: botID,
        sortby: 'delta',
        sortorder: 'asc'
    };

    const [sortPrefsJSON, setSortPrefsJSON] = useLocalStore('sortprefs', JSON.stringify([defaultsort]));
    const [sortPrefs, setSortPrefs] = useState(JSON.parse(sortPrefsJSON));

    if (!sortPrefs) window.location.reload();       // Avoid crash when cookies are cleared
    let match = sortPrefs.find((bot) => bot.botID === botID);

    if (!match) {
        let newSortPrefs = sortPrefs;
        newSortPrefs.push(defaultsort);
        setSortPrefs(newSortPrefs);
        setSortPrefsJSON(JSON.stringify(newSortPrefs));
        match = defaultsort;
    }

    const sortby = useRef(match.sortby);
    const sortorder = useRef(match.sortorder);

    const updateSortPrefs = (newprefs) => {
        let newSortPrefs = sortPrefs;
        let index = newSortPrefs.findIndex((bot) => bot.botID === botID);
        newSortPrefs[index] = newprefs;
        setSortPrefs(newSortPrefs);
        setSortPrefsJSON(JSON.stringify(newSortPrefs));
    }

    const sortPrefsUpdate = (newSortPrefs) => {
        setSortPrefs(JSON.parse(newSortPrefs));
    }

    useEffect(() => {
        // Listen to local store changes (when another page changes sort preferences)
        window.addEventListener('storage', (event) => {
            if (event.key === 'sortprefs') {
                const newSortPrefs = event.newValue;
                sortPrefsUpdate(newSortPrefs);
            }
        });

        return () => {
        }
    }, []);

    useEffect(() => {
        // Update headcell to show the right sorting arrow
        let newHeadCells = [...headCells];
        let header = newHeadCells.find((header) => header.id === sortby.current);
        header.order = sortorder.current;
        header.active = true;
        setHeadCells(newHeadCells);

        return () => {
        }
    }, [sortby.current, sortorder.current]);

    useEffect(() => {
        sortTable();

        return () => {
        }
    }, [balances]);

    useEffect(() => {
        // Remove selection if switching to automatic mode
        if (headless) {
            setSelected([]);
        }

        return () => {
        }
    }, [headless]);

    const sortTable = () => {
        let newRows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
        if (sortorder.current === 'asc')
            newRows.sort((a, b) => (a[sortby.current] < b[sortby.current]) ? 1 : ((b[sortby.current] < a[sortby.current]) ? -1 : 0));
        else
            newRows.sort((a, b) => (a[sortby.current] > b[sortby.current]) ? 1 : ((b[sortby.current] > a[sortby.current]) ? -1 : 0));

        setRows(newRows);
        handleTableUpdate(newRows);
    }

    const handleSort = (key) => {
        let newHeadCells = [...headCells];

        let previous = newHeadCells.find((header) => header.id === sortby.current);
        previous.active = false;

        let header = newHeadCells.find((header) => header.id === key);
        header.order = (header.order === 'desc') ? 'asc' : 'desc';
        header.active = true;
        setHeadCells(newHeadCells);

        sortorder.current = header.order;
        sortby.current = key;

        updateSortPrefs({
            botID: botID,
            sortby: sortby.current,
            sortorder: sortorder.current
        });
        sortTable();
    }

    const handleSelectAllClick = (event) => {
        if (selected.length === 0) {
            const newSelecteds = rows.map((n) => n.coin).filter(coin => coin !== configuration.basecoin);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleClick = (event, coin) => {
        if (!headless && coin !== configuration.basecoin) {
            const selectedIndex = selected.indexOf(coin);
            let newSelected = [];

            if (selectedIndex === -1) {
                newSelected = newSelected.concat(selected, coin);
            } else if (selectedIndex === 0) {
                newSelected = newSelected.concat(selected.slice(1));
            } else if (selectedIndex === selected.length - 1) {
                newSelected = newSelected.concat(selected.slice(0, -1));
            } else if (selectedIndex > 0) {
                newSelected = newSelected.concat(
                    selected.slice(0, selectedIndex),
                    selected.slice(selectedIndex + 1),
                );
            }
            setSelected(newSelected);
        }
    };

    const isSelected = (coin) => selected.indexOf(coin) !== -1;
    let tooSmallAmount = false;

    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <EnhancedTableToolbar selected={selected} headless={headless} ordertype={configuration.ordertype} portfolio={rows} handleOrder={handleOrder} setSelected={setSelected} basecoin={configuration.basecoin || ''} />
                {rows.length === 0 ? (
                    <Box pt={2} pl={2} pr={2} pb={4}>
                        <Skeleton animation="wave" />
                        <Skeleton animation="wave" />
                        <Skeleton animation="wave" />
                        <Skeleton animation="wave" />
                        <Skeleton animation="wave" />
                    </Box>
                ) : (
                    <TableContainer>
                        <Table
                            className={classes.table}
                            size="small"
                        >
                            <EnhancedTableHead
                                classes={classes}
                                headCells={headCells}
                                numSelected={selected.length}
                                onSelectAllClick={handleSelectAllClick}
                                onSort={handleSort}
                                rowCount={rows.length}
                                headless={headless}
                            />
                            <TableBody>
                                {rows.map((row, index) => {
                                    const isItemSelected = isSelected(row.coin);
                                    const labelId = `enhanced-table-checkbox-${index}`;
                                    let sidechip = <div style={{ color: '#999999', width: '120px' }}>-</div>;
                                    let tradelabel = formatResult(row.trade, 8);
                                    let tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />;

                                    let sidelabel = (row.orderstatus === 'none') ? row.side : row.orderstatus;
                                    if (row.reverse && row.orderstatus === 'none') sidelabel += ' (reverse)';

                                    if (row.side === 'sell') {
                                        if (row.distribution / row.target - 1 >= threshold / 100) {
                                            sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.error.dark, color: 'white', width: '120px' }} />
                                            if (!row.valid) {
                                                tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.warning.dark, color: 'white', width: '120px' }} />
                                                tooSmallAmount = true;
                                            }
                                        } else {
                                            sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />
                                        }
                                    } else if (row.side === 'buy') {
                                        if (row.distribution / row.target - 1 <= -threshold / 100) {
                                            sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.success.dark, color: 'white', width: '120px' }} />
                                            if (!row.valid) {
                                                tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.warning.dark, color: 'white', width: '120px' }} />
                                                tooSmallAmount = true;
                                            }
                                        } else {
                                            sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />
                                        }
                                    }

                                    if (sidelabel.includes('open') || sidelabel.includes('filled') || sidelabel.includes('canceled'))
                                        sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.info.main, color: 'white', width: '120px' }} />

                                    let checkbox;
                                    if (!headless && row.coin !== configuration.basecoin) {
                                        checkbox = <Checkbox
                                            checked={isItemSelected}
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    } else {
                                        checkbox = null;
                                    }

                                    const stablecoins = ['EUR', 'USD', 'BUSD', 'TUSD', 'USDT', 'USDC', 'USDP'];

                                    // Reduce number of decimals in case basecoin is a stable coin
                                    const dec = (stablecoins.includes(configuration.basecoin)) ? 2 : 8;

                                    return (
                                        <TableRow className={classes.tableRow}
                                            hover
                                            onClick={(event) => handleClick(event, row.coin)}
                                            role="checkbox"
                                            aria-checked={isItemSelected}
                                            tabIndex={-1}
                                            key={row.coin}
                                            selected={isItemSelected}
                                        >
                                            <TableCell padding="checkbox">
                                                {checkbox}
                                            </TableCell>
                                            <TableCell component="th" id={labelId} scope="row" padding="none">
                                                {row.coin}
                                            </TableCell>
                                            <TableCell align="right">{formatResult(row.number, 8)}</TableCell>
                                            <TableCell align="right">{formatResult(row.last, dec)}</TableCell>
                                            <TableCell align="right">{formatResult(row.value, dec)}</TableCell>
                                            <TableCell align="right">{formatResult(row.distribution, 2)}%</TableCell>
                                            <TableCell align="right">{formatResult(row.target, 2)}%</TableCell>
                                            <TableCell align="right">{formatResult(row.delta * 100, 2)}%</TableCell>
                                            <TableCell align="right">{tradechip}</TableCell>
                                            <TableCell align="center">{sidechip}</TableCell>
                                        </TableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                )}
                {(tooSmallAmount) && (
                    <Toolbar>
                        <Typography className={classes.title} variant="subtitle1" style={{ color: theme.palette.warning.dark }} component="div">
                            Trade amounts indicated in orange are too small to execute or exceed your available funds (when using a coin offset)
                        </Typography>
                    </Toolbar>
                )}
            </Paper>
        </div>
    );
}