import React, { useContext, useEffect, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TimelineView from "../components/TimelineView";
import StatusView from "../components/StatusView";
import LineChartCoins from '../components/LineChartCoins';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Hidden from '@material-ui/core/Hidden';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import DMSwitch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import { FiatContext } from '../context/FiatContext';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const StyledButton = withStyles({
  root: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
    '&:click': {
      backgroundColor: '#f00',
    },
    justifyContent: 'flex-start',
  },
})(Button);

const CoinAnalysis = () => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const configuration = useContext(ConfigurationContext)[0];
  const [open, setOpen] = useState(true);
  const [graph, setGraph] = useState('basecoin');
  const fiat = useContext(FiatContext)[0];

  const toggleGraph = () => {
    const newgraph = graph === 'basecoin' ? 'fiat' : 'basecoin';
    setGraph(newgraph);
  }

  useEffect(() => {
    setOpen(Object.keys(balances).length === 0);
    return () => {
    }
  }, [balances]);

  return (
    <div className={classes.root}>
      <Backdrop className={classes.backdrop} open={open}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4} lg={6}>
          <Typography variant="h3" noWrap>
            Coin Analysis
          </Typography>
        </Grid>
        <Hidden xsDown>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <TimelineView data={performance} />
          </Grid>
        </Hidden>
        <Grid item xs={12} sm={6} md={4} lg={3}>
          <StatusView />
        </Grid>
        <Grid container>
          <Grid container item xs={12} alignItems="center" justify="flex-start" style={{ maxWidth: '360px' }}>
            <Grid item xs={5}>
              <FormControlLabel
                onChange={toggleGraph}
                value={graph}
                control={<DMSwitch checked={graph === 'basecoin'} color="secondary" />}
                label={<Typography variant="button">Fiat ({fiat})</Typography>}
                labelPlacement="start"
              />
            </Grid>
            <Grid item xs={7}>
              <StyledButton disableRipple onClick={toggleGraph}><Typography variant="button">Base Coin ({configuration.basecoin})</Typography></StyledButton>
            </Grid>
          </Grid>
          <Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Paper style={{ height: '700px' }} className={classes.paper}>
            <LineChartCoins graph={graph} fiat={fiat} performance={performance} balances={balances} title={`Coin Price Changes vs ${graph === 'basecoin' ? configuration.basecoin : fiat} (%)`} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}

export default CoinAnalysis;