import { useContext } from 'react';
import { ResponsivePie } from '@nivo/pie';
import { useTheme } from '@material-ui/core/styles';
import { ConfigurationContext } from '../context/ConfigurationContext';

export default function PieChart(props) {
    const mode = useTheme().palette.type;
    const portfolio = props.portfolio || [];
    const configuration = useContext(ConfigurationContext)[0];
    const data = portfolio.map(function (coin) {
        return {
            'id': coin.coin,
            'label': coin.coin,
            'value': coin.distribution,
        }
    })

    return (
        <ResponsivePie
            theme={{
                tooltip: {
                    color: (mode === 'light') ? "#000000" : "#FFFFFF",
                    container: {
                        background: (mode === 'light') ? "#FFFFFF" : "#000000"
                    },
                },
            }}
            data={data}
            margin={{ top: 20, right: 80, bottom: 20, left: 80 }}
            innerRadius={0.5}
            padAngle={1}
            cornerRadius={7}
            colors={{ scheme: configuration ? configuration.colorscheme ? configuration.colorscheme : 'red_yellow_blue' : 'red_yellow_blue' }}
            borderWidth={1}
            borderColor={{ from: 'color', modifiers: [['darker', '0.2']] }}
            radialLabelsSkipAngle={10}
            radialLabelsTextColor={(mode === 'light') ? "#000000" : "#FFFFFF"}
            radialLabelsLinkColor={{ from: 'color' }}
            arcLinkLabelsSkipAngle={10}
            arcLinkLabelsTextColor={(mode === 'light') ? "#000000" : "#FFFFFF"}
        />
    )
}