import { ResponsiveBar } from '@nivo/bar'

export default function ColorBar(props) {
    const colorscheme = props.colorscheme;

    let numcolors;

    switch (colorscheme) {
        case 'red_yellow_blue':
            numcolors = 11;
            break;
        case 'red_yellow_green':
            numcolors = 11;
            break;
        case 'yellow_orange_red':
            numcolors = 9;
            break;
        case 'purpleRed_green':
            numcolors = 11;
            break;
        case 'pink_yellowGreen':
            numcolors = 11;
            break;
        case 'red_blue':
            numcolors = 11;
            break;
        case 'spectral':
            numcolors = 11;
            break;
        case 'paired':
            numcolors = 12;
            break;
        case 'category10':
            numcolors = 10;
            break;
        case 'dark2':
            numcolors = 8;
            break;
        case 'set1':
            numcolors = 9;
            break;
        case 'set2':
            numcolors = 8;
            break;
        case 'set3':
            numcolors = 12;
            break;
        default:
            numcolors = 0;
    }

    let range = {};
    let keys = [];

    for (let i = 0; i < numcolors; i++) {
        range['c' + i] = 10;
        keys.push('c' + i);
    }

    const data = [range];

    return (
        <ResponsiveBar
            data={data}
            keys={keys}
            height={20}
            width={20 * numcolors}
            margin={{ top: 0, right: 0, bottom: 0, left: 0 }}
            padding={0}
            layout="horizontal"
            valueScale={{ type: 'linear' }}
            colors={{ scheme: colorscheme }}
            axisTop={null}
            axisRight={null}
            axisBottom={null}
            axisLeft={null}
            enableLabel={false}
            isInteractive={false}
            legends={[]}
            barAriaLabel={null}
        />
    )
}