import { useContext } from 'react';
import { ResponsiveLine } from '@nivo/line'
import { useTheme } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import { ConfigurationContext } from '../context/ConfigurationContext';

export default function LineChart(props) {
    const mode = useTheme().palette.type;
    const configuration = useContext(ConfigurationContext)[0];

    let performance = props.performance;
    const balances = props.balances;
    const graph = props.graph;
    const fiat = props.fiat;
    const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];

    const usdBasecoins = ['USD', 'BUSD', 'TUSD', 'USDT', 'USDC', 'USDP'];

    let factor;

    // Sort alphabetically on coin names
    rows.sort((a, b) => (a['coin'] < b['coin']) ? 1 : ((b['coin'] < a['coin']) ? -1 : 0));

    let coins = [];
    let data = [];
    let prices = [];

    rows.forEach((coin, i) => {
        coins.push(coin.coin);
        prices[i] = {
            coin: coin.coin,
            initial: 0,
            data: [],
        };
    });

    if (performance !== []) {
        coins.forEach((coin, i) => {
            if (coin !== configuration.basecoin) {
                performance.forEach((day) => {
                    if (graph === 'basecoin') {
                        factor = 1;
                    } else {
                        if (configuration.basecoin === 'BTC' || configuration.basecoin === 'ETH') {
                            factor = fiat === 'EUR' ? day.eur : day.usd;
                        } else {
                            factor = usdBasecoins.includes(configuration.basecoin) ? fiat === 'EUR' ? day.eur / day.usd : 1 :
                                fiat === 'EUR' ? 1 : day.usd / day.eur;
                        }
                    }

                    day.coins.forEach((c) => {
                        if (c.coin === coin) {
                            if (prices[i].initial === 0) prices[i].initial = c.price * factor;
                            prices[i].data.push({
                                x: day.date,
                                y: (c.price * factor / prices[i].initial - 1) * 100,
                            });
                        }
                    });
                });
                data.push({
                    id: coin,
                    data: prices[i].data,
                })
            }
        });
    }

    let enoughData = false;
    if (data.length !== 0 && data[0].data.length > 1) enoughData = true;

    const getTheme = () => {
        return {
            axis: {
                ticks: {
                    line: {
                        stroke: (mode === 'light') ? "#000000" : "#FFFFFF"
                    },
                    text: { fill: (mode === 'light') ? "#000000" : "#FFFFFF" }
                },
                legend: {
                    text: {
                        fill: (mode === 'light') ? "#000000" : "#FFFFFF"
                    }
                }
            },
            crosshair: {
                line: {
                    stroke: (mode === 'light') ? "#000000" : "#FFFFFF",
                },
            },
            legends: {
                text: {
                    fill: (mode === 'light') ? "#000000" : "#FFFFFF",
                }
            },
            // tooltip: {
            //     container: {
            //         background: (mode === 'light') ? "#FFFFFF" : "#000000",
            //         color: (mode === 'light') ? "#000000" : "#FFFFFF",
            //     }
            // }
        };
    };

    return (
        <div className="wrapper">
            <div><b>{props.title}</b></div>
            <div className="graphContainerCoins">
                {(enoughData) ? (
                    <ResponsiveLine
                        data={data}
                        tooltip={(input) => {
                            return (
                                <div style={{
                                    background: (mode === 'light') ? "#FFFFFF" : "#000000",
                                    boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)',
                                    padding: '6px 10px 6px 10px'
                                }}>
                                    <span style={{ color: input.point.color }}>
                                        ▉ {input.point.serieId}:&nbsp;
                                    </span>
                                    <span style={{ color: (mode === 'light') ? "#000000" : "#FFFFFF" }}>
                                        {input.point.data.xFormatted} | <strong>{input.point.data.yFormatted}</strong>%
                                    </span>
                                </div>
                            )
                        }}
                        theme={getTheme()}
                        margin={{ top: 50, right: 110, bottom: 50, left: 80 }}
                        xScale={{
                            type: 'time',
                            format: '%Y/%m/%d',
                            useUTC: false,
                            precision: 'day',
                        }}
                        xFormat="time:%Y/%m/%d"
                        yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                        yFormat=" >-.2f"
                        curve="monotoneX"
                        lineWidth={4}
                        colors={{ scheme: configuration ? configuration.colorscheme ? configuration.colorscheme : 'red_yellow_blue' : 'red_yellow_blue' }}
                        axisTop={null}
                        axisRight={{
                            legend: '',
                            legendOffset: 70,
                            legendPosition: 'middle',
                        }}
                        axisBottom={null}
                        axisLeft={{
                            legend: 'Change (%)',
                            legendOffset: -70,
                            legendPosition: 'middle',
                        }}
                        enablePoints={false}
                        enableGridX={false}
                        enableGridY={false}
                        useMesh={true}
                        // enableSlices="y"
                        crosshairType="cross"
                        legends={[
                            {
                                anchor: 'bottom-right',
                                direction: 'column',
                                justify: false,
                                translateX: 120,
                                translateY: 0,
                                itemsSpacing: 0,
                                itemDirection: 'left-to-right',
                                itemWidth: 80,
                                itemHeight: 20,
                                itemOpacity: 0.75,
                                symbolSize: 12,
                                symbolShape: 'circle',
                                symbolBorderColor: 'rgba(0, 0, 0, .5)',
                                itemTextColor: "#ff0000",
                                effects: [
                                    {
                                        on: 'hover',
                                        style: {
                                            itemBackground: 'rgba(0, 0, 0, .03)',
                                            itemOpacity: 1
                                        }
                                    }
                                ]
                            }
                        ]}
                    />
                ) : (
                    <Alert severity="info">Not enough data yet to show graphs - check again later</Alert>
                )}
            </div>
        </div>
    )
}