import PieChartIcon from '@material-ui/icons/PieChart';
import ShowChartIcon from '@material-ui/icons/ShowChart';
import BarChartIcon from '@material-ui/icons/BarChart';
import HistoryIcon from '@material-ui/icons/History';
import SettingsIcon from '@material-ui/icons/Settings';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Portfolio from '../pages/Portfolio';
import PPerformance from '../pages/PPerformance';
import CoinAnalysis from '../pages/CoinAnalysis';
import EventHistory from '../pages/EventHistory';
import Configuration from '../pages/Configuration';
import Support from '../pages/Support';

const routesUp = [
  {
    path: 'portfolio',
    sidebarName: 'Portfolio',
    icon: PieChartIcon,
    component: Portfolio,
  },
  {
    path: 'performance',
    sidebarName: 'Performance',
    icon: BarChartIcon,
    component: PPerformance,
  },
  {
    path: 'coins',
    sidebarName: 'Coin Analysis',
    icon: ShowChartIcon,
    component: CoinAnalysis,
  },
  {
    path: 'history',
    sidebarName: 'Event History',
    icon: HistoryIcon,
    component: EventHistory,
  },
];

const routesDown = [
  {
    path: 'configuration',
    sidebarName: 'Configuration',
    icon: SettingsIcon,
    component: Configuration,
  },
  {
    path: 'support',
    sidebarName: 'Support Us',
    icon: FavoriteIcon,
    component: Support,
  }
];

export { routesUp, routesDown };