import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Hidden from '@material-ui/core/Hidden';
import BalancesTable from "../components/BalancesTable";
import PieChart from "../components/PieChart";
import { socket } from '../context/SocketContext';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import StatusView from "../components/StatusView";
import TimelineView from "../components/TimelineView";
import PerformanceSummary from "../components/PerformanceSummary";
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { ServerContext } from '../context/ServerContext';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

function CircularProgressWithLabel(props) {
  return (
    <Box position="relative" display="inline-flex">
      <CircularProgress variant="determinate" size={90} {...props} />
      <Box
        top={0}
        left={0}
        bottom={0}
        right={0}
        position="absolute"
        display="flex"
        alignItems="center"
        justifyContent="center"
      >
        <Typography variant="h5" component="div" color="textSecondary">{`${Math.round(
          props.value,
        )}%`}</Typography>
      </Box>
    </Box>
  );
}

const Portfolio = (props) => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const startingBalance = useContext(StartingBalanceContext)[0];
  const { access } = useContext(ServerContext)[0];
  const [open, setOpen] = useState(false);
  const [progress, setProgress] = useState(null);
  const [remainingSymbols, setRemainingSymbols] = useState([]);
  const [timeout, setTimeout] = useState(false);
  const [cancel, setCancel] = useState(false);
  const [portfolio, setPortfolio] = useState([]);

  useEffect(() => {
    socket.on("coin-loading-progress", (data) => {
      if (data.progress === -1) {
        setProgress(0);
      } else {
        setProgress(data.progress);
      }
      setRemainingSymbols(data.remainingSymbols);
      setTimeout(data.timeout);
      setCancel(data.cancel);
    });

    return () => {
      // socket.off("coin-loading-progress");
    }
  }, []);

  useEffect(() => {
    if (socket && startingBalance.length === 0 && access.allow) socket.emit("get-starting-balances", access.token);
    return () => {
    }
  }, []);

  useEffect(() => {
    if (progress === null || progress === 100) {
      setOpen(false);
    } else {
      setOpen(Object.keys(balances).length === 0);
    }

    return () => {
    }
  }, [balances, progress]);

  function handleUpdateTable(data) {
    setPortfolio(data);
  }

  function handleOrder(selected) {
    if (socket && access.allow) socket.emit("place-order", selected, access.token);
  }

  const showConfig = () => {
    setOpen(false);
    if (socket && access.allow) socket.emit("show-config", access.token);
  }

  const cancelLoading = () => {
    if (socket && access.allow) socket.emit("cancel-loading", access.token);
  }

  return (
    <div className={classes.root}>
      <Backdrop className={classes.backdrop} open={open}>
        {cancel ? (
          <Dialog
            open={open && cancel}
            fullWidth={true}
            maxWidth={'sm'}
          >
            <DialogContent>
              <DialogContentText>
                <Typography variant="overline" color="secondary">
                  Failed to load all trading pairs
                </Typography>
              </DialogContentText>
              <DialogContentText>
                <Grid container direction="column" >
                  <Typography variant="body1" color="inherit" noWrap>
                    The following trading pair(s) couldn't be loaded:
                  </Typography>
                  <Box p={1} />
                  {remainingSymbols.map(symbol => {
                    return (
                      <Typography variant="overline" color="inherit" display="block" noWrap>
                        <b>{">"} {symbol}</b>
                      </Typography>
                    )
                  })}
                  <Box p={1} />
                  <Typography variant="body1" color="inherit" noWrap>
                    The exchange may not support the trading pair(s)
                  </Typography>
                  <Typography variant="body1" color="inherit" noWrap>
                    To resolve this, exclude the coin(s) in the bot configuration
                  </Typography>
                </Grid>
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button variant="contained" color="secondary" style={{ width: '100px' }} onClick={showConfig}>
                OK
              </Button>
            </DialogActions>
          </Dialog>
        ) : (
          <Grid container spacing={3} direction="column" alignItems="center">
            <Grid item xs={12}>
              <Typography variant="h5" noWrap>
                Loading coin data
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <CircularProgressWithLabel color="inherit" variant="determinate" value={progress} />
            </Grid>
            {timeout ? (
              <Grid container direction="column" alignItems="center">
                <Box p={1} />
                <Typography variant="body1" noWrap>
                  This seems to be taking quite long
                </Typography>
                <Box p={1} />
                <Button variant="contained" color="primary" style={{ width: '120px' }} onClick={cancelLoading}>
                  Cancel
                </Button>
              </Grid>
            ) : (
              <Box height={88} />
            )}
          </Grid>
        )}
      </Backdrop>
      <Grid container spacing={3} alignItems="flex-start">
        <Grid item xs={12} md={4} lg={6}>
          <Typography variant="h3" noWrap>
            Portfolio
          </Typography>
        </Grid>
        <Hidden xsDown>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <TimelineView data={performance} />
          </Grid>
        </Hidden>
        <Grid item xs={12} sm={6} md={4} lg={3}>
          <StatusView />
        </Grid>
        <Grid item xs={12} sm={6} md={6} lg={3}>
          <Paper style={{ height: '100px', minWidth: '340px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='basecoin' />
          </Paper>
        </Grid>
        <Grid item xs={12} sm={6} md={6} lg={3}>
          <Paper style={{ height: '100px', minWidth: '340px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='fiat' />
          </Paper>
        </Grid>
        <Grid item xs={12} sm={6} md={6} lg={3}>
          <Paper style={{ height: '100px', minWidth: '340px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='net-basecoin' />
          </Paper>
        </Grid>
        <Grid item xs={12} sm={6} md={6} lg={3}>
          <Paper style={{ height: '100px', minWidth: '340px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='net-fiat' />
          </Paper>
        </Grid>
        <Hidden xsDown>
          <Grid item md={12} lg={8}>
            <BalancesTable balances={balances} onOrder={handleOrder} onUpdate={handleUpdateTable} />
          </Grid>
          <Grid container item md={12} lg={4}>
            <Grid item xs={12}>
              <Paper style={{ height: '500px' }} className={classes.paper}>
                <PieChart portfolio={portfolio} />
              </Paper>
            </Grid>
          </Grid>
        </Hidden>
      </Grid>
    </div >
  );
}

export default Portfolio;
