import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Link from '@material-ui/core/Link';
import InputAdornment from '@material-ui/core/InputAdornment';
import ColorBar from "../components/ColorBar";

const useStyles = makeStyles((theme) => ({
    formControl: {
        minWidth: '100%',
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    textinput: {
        width: '380px',
    },
    textinput2: {
        width: '200px',
    },
}));

export default function ConfigP4(props) {
    const classes = useStyles();

    const input = useContext(ConfigurationInputContext)[0];

    const [botname, setBotname] = useState(input.botname || '');
    const [showname, setShowname] = useState(input.showname || false);
    const [threshold, setThreshold] = useState(input.threshold || '3');
    const [colorscheme, setColorscheme] = useState(input.colorscheme || 'red_yellow_blue');
    const [ordertype, setOrdertype] = useState(input.ordertype || 'market');
    const [timeout, setTimeout] = useState(input.timeout || 20);
    const [headless, setHeadless] = useState(input.headless || false);
    const [cooldown, setCooldown] = useState(input.cooldown || 0);
    const [change24h, setChange24h] = useState(input.change24h || 0);
    const [emojiBuy, setEmojiBuy] = useState(input.emojibuy || '');
    const [emojiSell, setEmojiSell] = useState(input.emojisell || '');

    const onHeadless = (e) => {
        props.callback({
            field: 'headless',
            input: e.target.checked,
        });
        setHeadless(e.target.checked);
    }

    const onBotname = (e) => {
        props.callback({
            field: e.target.id,
            input: e.target.value
        });
        setBotname(e.target.value);
    }

    const onShowname = (e) => {
        props.callback({
            field: 'showname',
            input: e.target.checked,
        });
        setShowname(e.target.checked);
    }

    const onThreshold = (e) => {
        const pattern = /^[0-9]*\.?[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setThreshold(e.target.value);
        }
    }

    const onColorscheme = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setColorscheme(e.target.value);
    }

    const onOrdertype = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setOrdertype(e.target.value);
    }

    const onTimeout = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setTimeout(e.target.value);
    }

    const onEmojiBuy = (e) => {
        const pattern = /^[0-9,A-F]{0,5}$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setEmojiBuy(e.target.value);
        }
    }

    const onEmojiSell = (e) => {
        const pattern = /^[0-9,A-F]{0,5}$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setEmojiSell(e.target.value);
        }
    }

    const onCooldown = (e) => {
        const pattern = /^[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setCooldown(e.target.value);
        }
    }

    const onChange24h = (e) => {
        const pattern = /^-?[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setChange24h(e.target.value);
        }
    }

    const [open, setOpen] = useState(false);

    const handleTooltipClose = () => {
        setOpen(false);
    };

    const handleTooltipOpen = () => {
        setOpen(true);
    };

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        General Settings
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <ClickAwayListener onClickAway={handleTooltipClose}>
                        <HtmlTooltip
                            title={
                                <React.Fragment>
                                    <Box m={2}>
                                        <div><b>{"Bot Name"}</b></div>
                                        <div>{"Give your bot a descriptive name. This is especially useful if you run multiple bots, so you can easily recognize each."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Balancing Threshold"}</b></div>
                                        <div>{"The balancing threshold is used by Balance Bot to determine when to sell/buy coins. The threshold is the relative deviation from the target distribution at which point the bot will place an order to restore the coin to the target distribution."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Chart Color Scheme"}</b></div>
                                        <div>{"Select a color scheme to be used for the coin charts: the pie chart on the Portfolio page and the line chart on the Coin Analysis page."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Order Type"}</b></div>
                                        <div>{"Market Orders get executed immediately, but the price is not guaranteed. Limit Orders have a preset price, but it may take time before the order is filled. If the order is not filled after the select time out period, the order is canceled. High-Margin uses the ask price when selling, and the bid price when buying. Low-Margin uses the bid price when selling, and the ask price when buying. High-Margin limit orders may take longer to get filled, but at a better price."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Emojis"}</b></div>
                                        <div>{"Just because it's fun, you can configure an emoji to be shown in order notifications. By setting a different emoji for sell and buy orders, you can quickly identify the order type. You can find the complete list of emojis here "}<Link href="https://www.unicode.org/emoji/charts/full-emoji-list.html" target="_blank" rel="noopener" color="secondary">https://www.unicode.org/emoji/charts/full-emoji-list.html</Link>{". Copy and paste the code of your preferred emojis, but without the U+ prefix (e.g. enter 1F680, instead of U+1F680). If the code is correct, the emoji will show up to the right of the entry field."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Cooldown Trigger"}</b></div>
                                        <div>{"At times of high price volatility, Balance Bot may perform many trades to keep your portfolio balanced. This may not be preferred, because of the resulting trading fees. To avoid such situation, you may choose to trigger a 1 hour cooldown period, which kicks in when the set number of trades are performed within a 5 minute period."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Maximum 24h drop"}</b></div>
                                        <div>{"If a coin crashes in a short period of time, Balance Bot would sell your other coins in order to buy an increasing amount of the crashing coin. To avoid this from happening, enter the maximum 24h percentage change (e.g. -20%) below which the bot should be paused. If this point is reached, the bot will switch to manual mode, so you can investigate what's going on, and if needed exclude the coin in the configuration settings, before changing back to automatic mode again."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Automatic Mode"}</b></div>
                                        <div>{"You can operate your bot manually. In this mode, you will need to keep an eye on the dashboard and place manual orders when the dashboard indicates that a coin should be bought or sold. This is useful if you want to have full control, typically when you use Balance Bot for the first time and you want to get a feel for how it works."}</div>
                                        <div>&nbsp;</div>
                                        <div>{"Enabling automatic mode is a lot more practical though, as Balance Bot will run 24/7 and place orders automatically to keep your portfolio balanced."}</div>
                                        <div>&nbsp;</div>
                                        <div><i>{"Note: "}</i>{"You don't need to keep your browser open to run your bot in automatic mode. The application will keep running in the background. You can open the application in the browser at any time to check the status of your bot."}</div>
                                    </Box>
                                </React.Fragment>
                            }
                            onClose={handleTooltipClose}
                            open={open}
                            disableFocusListener
                            disableHoverListener
                            disableTouchListener
                            interactive
                        >
                            <IconButton onClick={open ? handleTooltipClose : handleTooltipOpen}>
                                <InfoOutlinedIcon color="secondary" />
                            </IconButton>
                        </HtmlTooltip>
                    </ClickAwayListener>
                </Grid>
            </Grid>
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <TextField
                        required
                        id="botname"
                        name="botname"
                        label="Bot Name"
                        helperText="Give your bot a name"
                        value={botname}
                        onChange={onBotname}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={6}>
                    <Box pt={1}>
                        <FormControlLabel
                            control={<Checkbox color="secondary" name="showname" checked={showname} onChange={onShowname} />}
                            label="Show in notifications (instead of bot ID)"
                        />
                    </Box>
                </Grid>
                <Grid item xs={6}>
                    <TextField
                        required
                        id="threshold"
                        name="threshold"
                        label="Balancing Threshold (%)"
                        helperText="Deviation from the target distribution at which the bot rebalances"
                        value={threshold}
                        onChange={onThreshold}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={3}>
                    <FormControl>
                        <InputLabel>Chart Color Scheme</InputLabel>
                        <Select
                            labelId="color-scheme"
                            name="colorscheme"
                            value={colorscheme}
                            onChange={onColorscheme}
                            className={classes.textinput2}
                        >
                            <MenuItem value={'red_yellow_blue'}>Red Yellow Blue</MenuItem>
                            <MenuItem value={'red_yellow_green'}>Red Yellow Green</MenuItem>
                            <MenuItem value={'yellow_orange_red'}>Yellow Orange Red</MenuItem>
                            <MenuItem value={'purpleRed_green'}>Purple Green</MenuItem>
                            <MenuItem value={'pink_yellowGreen'}>Pink Green</MenuItem>
                            <MenuItem value={'red_blue'}>Red Blue</MenuItem>
                            <MenuItem value={'spectral'}>Spectral</MenuItem>
                            <MenuItem value={'paired'}>Paired</MenuItem>
                            <MenuItem value={'category10'}>Category 10</MenuItem>
                            <MenuItem value={'dark2'}>Dark 2</MenuItem>
                            <MenuItem value={'set1'}>Set 1</MenuItem>
                            <MenuItem value={'set2'}>Set 2</MenuItem>
                            <MenuItem value={'set3'}>Set 3</MenuItem>
                        </Select>
                        <FormHelperText>Select a color scheme for the charts</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={3}>
                    <div style={{ height: '20px', marginTop: '20px' }}>
                        <ColorBar colorscheme={colorscheme} />
                    </div>
                </Grid>
                <Grid item xs={6}>
                    <FormControl>
                        <InputLabel>Order Type</InputLabel>
                        <Select
                            labelId="order-type"
                            name="ordertype"
                            value={ordertype}
                            onChange={onOrdertype}
                            className={classes.textinput}
                        >
                            <MenuItem value={'market'}>Market Order</MenuItem>
                            <MenuItem value={'limit-high'}>Limit Order &gt; High-Margin</MenuItem>
                            <MenuItem value={'limit-low'}>Limit Order &gt; Low-Margin</MenuItem>
                        </Select>
                        <FormHelperText>Select the order type to be used for trades</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={6}>
                    <FormControl>
                        <InputLabel>Limit Order Time Out</InputLabel>
                        <Select
                            labelId="limit-time-out"
                            name="timeout"
                            value={timeout}
                            onChange={onTimeout}
                            className={classes.textinput}
                            disabled={ordertype === 'market'}
                        >
                            <MenuItem value={10}>10 minutes</MenuItem>
                            <MenuItem value={20}>20 minutes</MenuItem>
                            <MenuItem value={30}>30 minutes</MenuItem>
                            <MenuItem value={60}>60 minutes</MenuItem>
                            <MenuItem value={90}>90 minutes</MenuItem>
                            <MenuItem value={120}>120 minutes</MenuItem>
                        </Select>
                        <FormHelperText>Select the maximum wait time for limit orders to be filled</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={4}>
                    <TextField
                        id="emojibuy"
                        name="emojibuy"
                        label="Emoji for Buy Order Notifications"
                        helperText="Configure an emoji to be shown in buy order notifications"
                        value={emojiBuy}
                        onChange={onEmojiBuy}
                        InputProps={{
                            startAdornment: <InputAdornment position="start">U+</InputAdornment>,
                        }}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={2}>
                    <Box pt={2}>
                        {emojiBuy !== '' && String.fromCodePoint(`0x${emojiBuy}`)}
                    </Box>
                </Grid>
                <Grid item xs={4}>
                    <TextField
                        id="emojisell"
                        name="emojisell"
                        label="Emoji for Sell Order Notifications"
                        helperText="Configure an emoji to be shown in sell order notifications"
                        value={emojiSell}
                        onChange={onEmojiSell}
                        InputProps={{
                            startAdornment: <InputAdornment position="start">U+</InputAdornment>,
                        }}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={2}>
                    <Box pt={2}>
                        {emojiSell !== '' && String.fromCodePoint(`0x${emojiSell}`)}
                    </Box>
                </Grid>
                <Grid item xs={6}>
                    <TextField
                        required
                        id="cooldown"
                        name="cooldown"
                        label="Cooldown Trigger"
                        helperText="After this many orders within 5 minutes, Balance Bot will go into cooldown mode (set to 0 to disable)"
                        value={cooldown}
                        onChange={onCooldown}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={6}>
                    <TextField
                        required
                        id="change24h"
                        name="change24h"
                        label="Maximum 24h Drop (%)"
                        helperText="If a coin's 24h change goes below this maximum, your bot will be paused (set to 0 to disable)"
                        value={change24h}
                        onChange={onChange24h}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="headless" checked={headless} onChange={onHeadless} />}
                        label="Run bot in automatic mode"
                    />
                </Grid>
            </Grid>
        </React.Fragment>
    );
}