import React, { useState, useEffect, useContext } from "react";
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { FiatContext } from '../context/FiatContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { PerformanceContext } from '../context/PerformanceContext';

export default function StatusView(props) {
    const startingBalances = useContext(StartingBalanceContext)[0];
    const performance = useContext(PerformanceContext)[0];
    const configuration = useContext(ConfigurationContext)[0];
    const [performanceSummary, setPerformanceSummary] = useState({ value: '0', change: '0' });
    const [dataloaded, setDataloaded] = useState(false);
    const balances = props.balances;
    const type = props.type;
    const fiat = useContext(FiatContext)[0];
    const basecoin = configuration.basecoin;
    const currency = (type === 'basecoin') ? basecoin : (type === 'fiat' || type === 'net-fiat') ? fiat : `Net ${basecoin}`;
    const leftTxt = (type === 'net-basecoin') ? `Net effect (${basecoin})` : (type === 'net-fiat') ? `Net effect (${currency})` : `${currency} value`;
    const rightTxt = (type === 'net-basecoin' || type === 'net-fiat') ? 'Net effect (%)' : `${currency} change`;

    const usdBasecoins = ['USD', 'BUSD', 'TUSD', 'USDT', 'USDC', 'USDP'];

    useEffect(() => {
        setDataloaded(startingBalances.length !== 0);

        let rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
        let currentValue = 0;
        rows.forEach((coin) => {
            currentValue += coin.number * coin.last;
        });

        if (type === 'fiat' || type === 'net-fiat') {
            if (basecoin === 'BTC' || basecoin === 'ETH') {
                currentValue = currentValue * ((fiat === 'EUR') ? balances.eur : balances.usd);
            } else if (usdBasecoins.includes(basecoin)) {
                currentValue = currentValue * ((fiat === 'EUR') ? balances.eur / balances.usd : 1);
            } else if (basecoin === 'EUR') {
                currentValue = currentValue * ((fiat === 'EUR') ? 1 : balances.usd / balances.eur);
            }
        }

        let change;

        if (currentValue !== 0) {
            if (type === 'basecoin') change = (currentValue / startingBalances.basecoin - 1) * 100;
            else if (fiat === 'EUR') change = (currentValue / startingBalances.eur - 1) * 100;
            else change = (currentValue / startingBalances.usd - 1) * 100;

            let hodlvalue = 0;

            if (startingBalances.coins) rows.forEach((coin) => {
                let c = startingBalances.coins.find(c => c.coin === coin.coin);
                // Old performance files may have coins with number = 0, and then netvalue shouldn't be impacted
                // Could also happen when multiple bots track the same portfolio, and a coin is excluded in one, but not the other(s)
                if (c && c.coin && coin.number !== 0) hodlvalue += c.number * coin.last;
            });

            let netoffset = 0;
            if (performance.length !== 0) {
                let perftoday = performance[performance.length - 1];
                netoffset = perftoday.netoffset;
            }

            hodlvalue -= netoffset;

            if (type === 'net-basecoin') {
                change = ((currentValue - hodlvalue) / startingBalances.basecoin) * 100;
                currentValue = currentValue - hodlvalue;
            }

            if (type === 'net-fiat') {
                let starting = (fiat === 'EUR') ? startingBalances.eur : startingBalances.usd;

                if (basecoin === 'BTC' || basecoin === 'ETH') {
                    hodlvalue = (fiat === 'EUR') ? hodlvalue * balances.eur : hodlvalue * balances.usd;
                } else if (usdBasecoins.includes(basecoin)) {
                    hodlvalue = (fiat === 'EUR') ? hodlvalue * balances.eur / balances.usd : hodlvalue;
                } else if (basecoin === 'EUR') {
                    hodlvalue = (fiat === 'EUR') ? hodlvalue : hodlvalue * balances.usd / balances.eur;
                }

                change = ((currentValue - hodlvalue) / starting) * 100;
                currentValue = currentValue - hodlvalue;
            }

            if (change) {
                if (change < 100) change = change.toFixed(2);
                else if (change < 1000) change = change.toFixed(1);
                else change = change.toFixed(0);
            } else {
                change = Number('0').toFixed(2);
            }
            if (currentValue) {
                if (Math.abs(currentValue) < 0.000001) currentValue = currentValue.toFixed(2)
                else if (Math.abs(currentValue) < 0.1) currentValue = currentValue.toFixed(6)
                else if (Math.abs(currentValue) < 1) currentValue = currentValue.toFixed(5)
                else if (Math.abs(currentValue) < 10) currentValue = currentValue.toFixed(4)
                else if (Math.abs(currentValue) < 100) currentValue = currentValue.toFixed(3)
                else if (Math.abs(currentValue) < 1000) currentValue = currentValue.toFixed(2);
                else currentValue = currentValue.toFixed(2);
            } else {
                currentValue = Number('0').toFixed(2);
            }
        } else {
            change = Number('0').toFixed(2);
        }

        setPerformanceSummary({
            value: currentValue,
            change: change,
        });
    }, [balances, startingBalances, fiat]);

    return (
        <Grid container alignItems="center">
            <Grid container item direction="column" alignItems="center" xs={6}>
                <Grid item xs={12}><Typography variant="h4">{dataloaded ? performanceSummary.value : '0.00'}</Typography></Grid>
                <Grid item xs={12}><Typography>{leftTxt}</Typography></Grid>
            </Grid>
            <Grid container item direction="column" alignItems="center" xs={6}>
                <Grid item xs={12}><Typography variant="h4">{dataloaded ? performanceSummary.change : '0.00'}%</Typography></Grid>
                <Grid item xs={12}><Typography>{rightTxt}</Typography></Grid>
            </Grid>
        </Grid>
    )
}