import React, { useState, useEffect } from 'react';
import { ResponsiveLine } from '@nivo/line'
import "./LineChart.css";
import { useTheme } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';

export default function LineChart(props) {
    const mode = useTheme().palette.type;
    let data1 = props.data1;
    let data2 = props.data2;
    const [title, setTitle] = useState('');

    useEffect(() => {
        if (data1 && data1.length !== 0) setTitle(data1[0].id);
        else if (data2 && data2.length !== 0) setTitle(data2[0].id);
        return () => {
        }
    }, [data1, data2]);

    let enoughData = false;
    if (data2 && data2[0].data.length > 1) enoughData = true;

    const getTheme = () => {
        return {
            axis: {
                ticks: {
                    line: {
                        stroke: (mode === 'light') ? "#000000" : "#FFFFFF"
                    },
                    text: { fill: (mode === 'light') ? "#000000" : "#FFFFFF" }
                },
                legend: {
                    text: {
                        fill: (mode === 'light') ? "#000000" : "#FFFFFF"
                    }
                }
            },
            crosshair: {
                line: {
                    stroke: (mode === 'light') ? "#000000" : "#FFFFFF",
                },
            },
            // tooltip: {
            //     container: {
            //         background: (mode === 'light') ? "#FFFFFF" : "#000000",
            //         color: (mode === 'light') ? "#000000" : "#FFFFFF",
            //     }
            // }
        };
    };

    const getAxis1 = () => {
        if (data1.length !== 0) {
            return {
                legend: data1[0].id,
                legendOffset: -80,
                legendPosition: 'middle',
            }
        } else {
            // In case data1 is not set, just use axis2 on both sides
            return getAxis2;
        }
    }

    const getAxis2 = () => {
        if (data2.length !== 0) {
            return {
                legend: data2[0].id,
                legendOffset: 70,
                legendPosition: 'middle',
            }
        } else {
            return null;
        }
    }

    return (
        <div className="wrapper">
            <div><b>{title}</b></div>
            <div className="secondGraph">
                {(data1 && enoughData) ? (
                    <ResponsiveLine
                        data={data1}
                        tooltip={(input) => {
                            return (
                                <div style={{
                                    background: (mode === 'light') ? "#FFFFFF" : "#000000",
                                    boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)',
                                    padding: '6px 10px 6px 10px'
                                }}>
                                    <div style={{ color: 'rgb(211,47,47)' }}>
                                        ▉ {input.point.serieId}:&nbsp;
                                    </div>
                                    <div style={{ color: (mode === 'light') ? "#000000" : "#FFFFFF" }}>
                                        {input.point.data.xFormatted} | <strong>{input.point.data.yFormatted}</strong>
                                    </div>
                                </div>
                            )
                        }}
                        theme={getTheme()}
                        margin={{ top: 40, right: 90, bottom: 50, left: 90 }}
                        xScale={{
                            type: 'time',
                            format: '%Y/%m/%d',
                            useUTC: false,
                            precision: 'day',
                        }}
                        xFormat="time:%Y/%m/%d"
                        yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                        yFormat=" >-.6f"
                        curve="monotoneX"
                        colors={['rgb(255,255,255,0)']}
                        axisTop={null}
                        axisRight={null}
                        axisBottom={null}
                        axisLeft={getAxis1()}
                        enablePoints={false}
                        enableGridX={false}
                        enableGridY={false}
                        useMesh={true}
                        // enableSlices="y"
                        crosshairType="cross"
                    />
                ) : (null)}
            </div>
            <div className="graphContainer">
                {(data2 && enoughData) ? (
                    <ResponsiveLine
                        data={data2}
                        theme={getTheme()}
                        margin={{ top: 40, right: 90, bottom: 50, left: 90 }}
                        xScale={{
                            type: 'time',
                            format: '%Y/%m/%d',
                            useUTC: false,
                            precision: 'day',
                        }}
                        xFormat="time:%Y/%m/%d"
                        yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                        yFormat=" >-.6f"
                        curve="monotoneX"
                        lineWidth={4}
                        colors={['rgb(211,47,47)']}
                        axisTop={null}
                        axisBottom={null}
                        axisRight={getAxis2()}
                        axisLeft={null}
                        enablePoints={false}
                        enableGridX={false}
                        enableGridY={false}
                        useMesh={true}
                        // enableSlices="y"
                        crosshairType="cross"
                    />
                ) : (
                    <Alert severity="info">Not enough data yet to show graphs - check again later</Alert>
                )}
            </div>
        </div>
    )
}