import React, { useContext, useEffect, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TimelineView from "../components/TimelineView";
import StatusView from "../components/StatusView";
import LineChart from '../components/LineChart';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { FiatContext } from '../context/FiatContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ServerContext } from '../context/ServerContext';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import { socket } from '../context/SocketContext';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import DMSwitch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const StyledButton = withStyles({
  root: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
    '&:click': {
      backgroundColor: '#f00',
    },
    justifyContent: 'flex-start',
  },
})(Button);

const Performance = () => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const startingBalance = useContext(StartingBalanceContext)[0];
  const fiat = useContext(FiatContext)[0];
  const configuration = useContext(ConfigurationContext)[0];
  const [data1, setData1] = useState({});
  const [data2, setData2] = useState({});
  const { access } = useContext(ServerContext)[0];
  const [open, setOpen] = useState(true);
  const [graph, setGraph] = useState('normalized');

  const usdBasecoins = ['USD', 'BUSD', 'TUSD', 'USDT', 'USDC', 'USDP'];

  const toggleGraph = () => {
    const newgraph = graph === 'normalized' ? 'absolute' : 'normalized';
    setGraph(newgraph);
  }

  useEffect(() => {
    if (socket && startingBalance.length === 0 && access.allow) socket.emit("get-starting-balances", access.token);
    return () => {
    }
  }, []);

  useEffect(() => {
    setOpen(Object.keys(balances).length === 0);
    return () => {
    }
  }, [balances]);

  let values = [];
  let fiatValues = [];
  let change = [];
  let fiatChange = [];
  let valuesNorm = [];
  let fiatValuesNorm = [];
  let changeNorm = [];
  let fiatChangeNorm = [];
  let netvalues = [];
  let netchange = [];
  let netvaluesfiat = [];
  let netchangefiat = [];
  let newdata1 = {};
  let newdata2 = {};

  useEffect(() => {
    computeGraphs();

    return () => {
    }
  }, [balances, performance, startingBalance, fiat]);

  const getPerf = (day, normalize) => {
    let value = 0;
    let fiatValue = 0;

    let multiplier;

    if (configuration.basecoin === 'BTC' || configuration.basecoin === 'ETH') {
      multiplier = ((fiat === 'EUR') ? day.eur : day.usd);
    } else if (usdBasecoins.includes(configuration.basecoin)) {
      multiplier = ((fiat === 'EUR') ? day.eur / day.usd : 1);
    } else if (configuration.basecoin === 'EUR') {
      multiplier = ((fiat === 'EUR') ? 1 : day.usd / day.eur);
    }

    day.coins.forEach((coin) => {
      value += coin.number * coin.price;
      fiatValue += coin.number * coin.price * multiplier;
    });

    if (normalize) {
      value += day.offset;
      fiatValue += day.offset * multiplier;
    }

    return [value, fiatValue];
  }

  const computeGraphs = () => {
    let initialValue = 0;
    let initialFiatValue = 0;
    let initialValueNorm = 0;
    let initialFiatValueNorm = 0;

    if (startingBalance.length !== 0 && startingBalance.coins.length !== 0) {
      initialValue = startingBalance.basecoin;
      initialFiatValue = (fiat === 'EUR') ? startingBalance.eur : startingBalance.usd;

      if (performance.length !== 0) {
        performance.forEach((day, i) => {
          const [value, fiatValue] = getPerf(day, false);
          const [valueNorm, fiatValueNorm] = getPerf(day, true);;

          if (i === 0) {
            initialValueNorm = valueNorm;
            initialFiatValueNorm = fiatValueNorm;
          }

          let netvalue = 0;
          let netvaluefiat = 0;

          day.coins.forEach((coin) => {
            // Old performance files may have coins on day 0 that were added later, and don't get an initial value
            if (!coin.initial) coin.initial = 0;
            // Old performance files may have coins with number = 0, and then netvalue shouldn't be impacted
            // Could also happen when multiple bots track the same portfolio, and a coin is excluded in one, but not the other(s)
            if (coin.number !== 0) netvalue += (coin.number - coin.initial) * coin.price;
          });
          netvalue += day.netoffset;

          if (configuration.basecoin === 'BTC' || configuration.basecoin === 'ETH') {
            netvaluefiat = netvalue * ((fiat === 'EUR') ? day.eur : day.usd);
          } else if (usdBasecoins.includes(configuration.basecoin)) {
            netvaluefiat = netvalue * ((fiat === 'EUR') ? day.eur / day.usd : 1);
          } else if (configuration.basecoin === 'EUR') {
            netvaluefiat = netvalue * ((fiat === 'EUR') ? 1 : day.usd / day.eur);
          }

          values.push({
            x: day.date,
            y: value,
          });
          change.push({
            x: day.date,
            y: (value / initialValue - 1) * 100,
          });
          fiatValues.push({
            x: day.date,
            y: fiatValue,
          });
          fiatChange.push({
            x: day.date,
            y: (fiatValue / initialFiatValue - 1) * 100,
          });
          valuesNorm.push({
            x: day.date,
            y: valueNorm,
          });
          changeNorm.push({
            x: day.date,
            y: (valueNorm / initialValueNorm - 1) * 100,
          });
          fiatValuesNorm.push({
            x: day.date,
            y: fiatValueNorm,
          });
          fiatChangeNorm.push({
            x: day.date,
            y: (fiatValueNorm / initialFiatValueNorm - 1) * 100,
          });
          netvalues.push({
            x: day.date,
            y: netvalue,
          });
          netchange.push({
            x: day.date,
            y: (netvalue / initialValue) * 100,
          });
          netvaluesfiat.push({
            x: day.date,
            y: netvaluefiat,
          });
          netchangefiat.push({
            x: day.date,
            y: (netvaluefiat / initialFiatValue) * 100,
          });
        });

        newdata1.basecoin = [
          {
            id: `Total Value (${configuration.basecoin})`,
            data: values,
          }
        ];
        newdata2.basecoin = [
          {
            id: `${configuration.basecoin} Change (%)`,
            data: change,
          }
        ];
        newdata1.fiat = [
          {
            id: `Total Value (${fiat})`,
            data: fiatValues,
          }
        ];
        newdata2.fiat = [
          {
            id: `${fiat} Change (%)`,
            data: fiatChange,
          }
        ];
        newdata1.basecoinnorm = [
          {
            id: `Normalized Value (${configuration.basecoin})`,
            data: valuesNorm,
          }
        ];
        newdata2.basecoinnorm = [
          {
            id: `Normalized ${configuration.basecoin} Change (%)`,
            data: changeNorm,
          }
        ];
        newdata1.fiatnorm = [
          {
            id: `Normalized Value (${fiat})`,
            data: fiatValuesNorm,
          }
        ];
        newdata2.fiatnorm = [
          {
            id: `Normalized ${fiat} Change (%)`,
            data: fiatChangeNorm,
          }
        ];
        newdata1.net = [
          {
            id: `Net Balancing Effect (${configuration.basecoin})`,
            data: netvalues,
          }
        ];
        newdata2.net = [
          {
            id: 'Net Balancing Effect (%)',
            data: netchange,
          }
        ];
        newdata1.netfiat = [
          {
            id: `Net Balancing Effect (${fiat})`,
            data: netvaluesfiat,
          }
        ];
        newdata2.netfiat = [
          {
            id: 'Net Balancing Effect (%)',
            data: netchangefiat,
          }
        ];
        setData1(newdata1);
        setData2(newdata2);
      }
    }
  }

  return (
    <div className={classes.root}>
      <Backdrop className={classes.backdrop} open={open}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4} lg={6}>
          <Typography variant="h3" noWrap>
            Performance
          </Typography>
        </Grid>
        <Hidden xsDown>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <TimelineView data={performance} />
          </Grid>
        </Hidden>
        <Grid item xs={12} sm={6} md={4} lg={3}>
          <StatusView />
        </Grid>
        <Grid container>
          <Grid container item xs={12} alignItems="center" justify="flex-start" style={{ maxWidth: '360px' }}>
            <Grid item xs={5}>
              <FormControlLabel
                onChange={toggleGraph}
                value={graph}
                control={<DMSwitch checked={graph === 'normalized'} color="secondary" />}
                label={<Typography variant="button">Absolute</Typography>}
                labelPlacement="start"
              />
            </Grid>
            <Grid item xs={7}>
              <StyledButton disableRipple onClick={toggleGraph}><Typography variant="button">Normalized</Typography></StyledButton>
            </Grid>
          </Grid>
          <Grid>
          </Grid>
        </Grid>
        {(graph === 'absolute') ? (
          <React.Fragment>
            <Grid item xs={12} sm={12} md={6} lg={6}>
              <Paper style={{ height: '360px' }} className={classes.paper}>
                <LineChart data1={data1.basecoin} data2={data1.basecoin} type={'basecoin'} />
              </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6}>
              <Paper style={{ height: '360px' }} className={classes.paper}>
                <LineChart data1={data1.fiat} data2={data1.fiat} type={'fiat'} />
              </Paper>
            </Grid>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <Grid item xs={12} sm={12} md={6} lg={6}>
              <Paper style={{ height: '360px' }} className={classes.paper}>
                <LineChart data1={data2.basecoinnorm} data2={data2.basecoinnorm} type={'basecoin'} />
              </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6}>
              <Paper style={{ height: '360px' }} className={classes.paper}>
                <LineChart data1={data2.fiatnorm} data2={data2.fiatnorm} type={'fiat'} />
              </Paper>
            </Grid>
          </React.Fragment>
        )}
        <Grid item xs={12} sm={12} md={6} lg={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.net} data2={data2.net} type={'net-result'} />
          </Paper>
        </Grid>
        <Grid item xs={12} sm={12} md={6} lg={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.netfiat} data2={data2.netfiat} type={'net-result-fiat'} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}

export default Performance;