import React, { useContext } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import StepConnector from '@material-ui/core/StepConnector';
import Paper from '@material-ui/core/Paper';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { add } from 'date-fns'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        height: '68px',
        minWidth: '340px',
    },
    stepper: {
        padding: theme.spacing(1),
    }
}));

export default function TimelineView(props) {
    const classes = useStyles();

    const configuration = useContext(ConfigurationContext)[0];
    const performance = props.data || [];

    let numdays = '--';
    let startdate = '----/--/--';

    const shiftedDate = () => {
        const now = new Date();
        let adjusted = now;
        if (configuration.timeshift && configuration.timeshift !== 0) adjusted = add(now, { hours: configuration.timeshift });
        return adjusted;
    }

    if (performance.length !== 0) {
        let date1 = new Date(configuration.startdate);
        let date2 = shiftedDate();
        var timeDiff = date2.getTime() - date1.getTime();

        numdays = Math.round(timeDiff / (1000 * 3600 * 24));
        startdate = date1.getFullYear() + "/" + (date1.getMonth() + 1).toString().padStart(2, '0') + "/" + date1.getDate().toString().padStart(2, '0');
    }

    return (
        <Paper className={classes.paper}>
            <Grid container alignItems="center" >
                <Grid item xs={3}><Typography align={'right'}>{startdate}</Typography></Grid>
                <Grid item xs={6}>
                    <Stepper className={classes.stepper} alternativeLabel activeStep={1} connector={<ColorlibConnector />}>
                        <Step key={0}>
                            <StepLabel StepIconComponent={ColorlibStepIcon}></StepLabel>
                        </Step>
                        <Step key={1}>
                            <StepLabel StepIconComponent={ColorlibStepIcon}></StepLabel>
                        </Step>
                    </Stepper>
                </Grid>
                <Grid item xs={3}><Typography><b>{numdays}</b> {(numdays === 1) ? 'day' : 'days'}</Typography></Grid>
            </Grid>
        </Paper >
    )
}

const ColorlibConnector = withStyles({
    alternativeLabel: {
        top: 22,
    },
    active: {
        '& $line': {
            backgroundImage:
                'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
        },
    },
    completed: {
        '& $line': {
            backgroundImage:
                'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
        },
    },
    line: {
        height: 3,
        border: 0,
        backgroundColor: '#eaeaf0',
        borderRadius: 1,
    },
})(StepConnector);

const useColorlibStepIconStyles = makeStyles({
    root: {
        backgroundColor: '#ccc',
        zIndex: 1,
        color: '#fff',
        width: 50,
        height: 50,
        display: 'flex',
        borderRadius: '50%',
        justifyContent: 'center',
        alignItems: 'center',
    },
    active: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
        boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
    },
});

function ColorlibStepIcon(props) {
    const classes = useColorlibStepIconStyles();
    const { active, completed } = props;

    const icons = {
        1: <RadioButtonCheckedIcon />,
        2: <ArrowForwardIosIcon />,
    };

    return (
        <div
            className={clsx(classes.root, {
                [classes.active]: active,
                [classes.completed]: completed,
            })}
        >
            {icons[String(props.icon)]}
        </div>
    );
}
